$day-block-size: 100px;
$base-padding: 10px;
$day-in-week: 7;
$gap-between-days-blocks: 1px;
$max-day-number: 31;
$min-day-number: 28;
$move-day-block-up: 20px;
$day-block-background-color: #eee;
$day-block-hover-background-color: pink;
$days-names:
  "mon" 1,
  "tue" 2,
  "wed" 3,
  "thu" 4,
  "fri" 5,
  "sat" 6,
  "sun" 7;
